import React from 'react';
import {Header} from './components/Header';
import { BrowserRouter, Route } from 'react-router-dom';
import {AddTodo} from './components/pages/AddTodo';
import {Calendar} from './components/pages/Calendar';
import {Notes} from './components/pages/Notes';
import {About} from './components/pages/About';
import {MainTask} from './components/pages/MainTask';

import './App.css';
import { Container, Row, Col, Button, Alert, Breadcrumb, Card, Form } from 'react-bootstrap';
import "bootswatch/dist/minty/bootstrap.min.css"

function App() {

  const props = {
    tasks: [
      { id: 1, text: 'Finish hackathon project and fix long task names', deadline: 2, importance: 0.50 },
      { id: 2, text: 'CSC236 assignment', deadline: 4, importance: 0.40 },
      { id: 3, text: 'Apply for internship', deadline: 40, importance: 0.90 },
      { id: 4, text: 'Do laundry', deadline: 5, importance: 0.20 },
    ]}
    // addTask: (id) => ,
    // deleteTask: ,

  return (
    <Container style={{width: "100%"}}>
      <div width = "100%">
      <header className="App-header" width = "100%">
        <Header />
        <BrowserRouter>
            <Route exact path = "/planno2" render={(routeProps) =>
              <MainTask {...routeProps} tasks = {props.tasks}/>
            } />
            <Route path = "/planno2/addtodo" render={(routeProps) =>
              <AddTodo {...routeProps} tasks = {props.tasks}/>
            } />
            <Route path = "/planno2/cal" component={Calendar} />
            <Route path = "/planno2/notes" component={Notes} />
            <Route path = "/planno2/about" component={About} />
        </BrowserRouter>
      </header>
    </div>
    </Container>
  );
}

export default App;
